@-webkit-keyframes appear {
  0%{ opacity: 0; left: -200px;}
  100%{ opacity: 1;  left:0;}
}
@keyframes appear {
  0%{ opacity: 0; left: -200px;}
  100%{ opacity: 1;  left:0;}
}
@-webkit-keyframes appear1 {
  0%{ opacity: 0;}
  100%{ opacity: 1;}
}
@keyframes appear1 {
  0%{ opacity: 0;}
  100%{ opacity: 1;}
}
@-webkit-keyframes appear2 {
  0%{ opacity: 0; top: 100%;}
  100%{ opacity: 1;  top:0%;}
}
@keyframes appear2 {
  0%{ opacity: 0; top: 100%;}
  100%{ opacity: 1;  top:0%;}
}
@-webkit-keyframes appear3 {
  0%{ opacity: 0; top: 300px;}
  100%{ opacity: 1;  top:50px;}
}
@keyframes appear3 {
  0%{ opacity: 0; top: 300px;}
  100%{ opacity: 1;  top:50px;}
}
@-webkit-keyframes disappear {
  0%{ opacity: 1; }
  100%{ opacity: 0;  top:400px;}
}
@keyframes disappear {
  0%{ opacity: 1; }
  100%{ opacity: 0;  top:400px;}
}
@-webkit-keyframes slideInLeft {
  0%{left:-15vw;}
  100%{left:0;}
}
@keyframes slideInLeft {
  0%{left:-15vw;}
  100%{left:0;}
}
@-webkit-keyframes slideInRight {
  0%{right:-15vw;}
  100%{right:0;}
}
@keyframes slideInRight {
  0%{right:-15vw;}
  100%{right:0;}
}
@-webkit-keyframes contract {
  0%{width: 100vw; margin-left: 0vw;}
  100%{width: 70vw; margin-left: 15vw;}
}
@keyframes contract {
  0%{width: 100vw; margin-left: 0vw;}
  100%{width: 70vw; margin-left: 15vw;}
}
body{
  width: 100vw;
  margin: 0;
  overflow-x: hidden;
}
.body-container{
  height:  100vh;
  position: fixed;
}
.left-panel-main{
  width: 15vw ;
  left:0;
  float: left;
  height: 100vh;
  position: fixed;
  top:0;
  display: inline-block;
  text-align: center;
  background-color: #eee;
  background-image: url("https://www.transparenttextures.com/patterns/light-honeycomb-dark.png");
  background:cover;
  background-repeat: all;
  text-align: center;
}
.labeler-main{
  margin-top: 150px;
  height:100%;
}
.label-text{
  position: absolute;
  top:0;
  width:15vw;
  text-align: center;
}
.right-border-panel{
  width: 15vw;
  background-color: #eee;
background-image: url("https://www.transparenttextures.com/patterns/light-honeycomb-dark.png");
  background:cover;
  background-repeat: all;
  float: right;
  
  height: 100vh;
  position: fixed;
  z-index: 2;
  display: inline-block;
}
.logo{
  height: 120px;
  position: absolute;
  left:calc(7.5vw - 60px);
  top:-20px;
}
.nav-li{
  color: black;
  text-align: center;
  padding: 10px;
  margin-right: 40px;
  font-size: 20px;
  font-weight: 800;
  display: inline-block;
}

.nav-container{
  width: 100vw;
  position: fixed;
  z-index: 9;
  display: -ms-flexbox;
  display: flex;
  background:#079;
  top:20px;
  border-top: 2px solid white;
  border-bottom: 2px solid white;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.nav-li:hover{
  cursor: pointer;
}
.clickable:hover{
  background-color: rgba(255,255,255,.5)
}
.nav-pers,
.nav-prof{
  font-size: 12px;
  font-weight: normal;
  padding: 3px;
  display: none;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}
.nav-pers-container:hover .nav-pers{
  display:block;
}
.nav-prof-container:hover .nav-prof{
  display: block;
}
.content-block-container{
   padding: 20px;
  margin-top: 5vh;
  height: 40vh;
  margin-bottom: 15vh;
  display: inline-block;
  width: 90%;
  margin-left: auto;
  position: relative;
}
.content-container{ 
  visibility: visible;  
  position: relative;
  height: 100%;
  
}


.show,
.show1,
.show2,
.show3{
  visibility: visible;  
  -webkit-animation-fill-mode: forwards;  
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s; 
}
.show{
  -webkit-animation-name: appear;
          animation-name: appear;
}
.show1{
  -webkit-animation-name: appear1;
          animation-name: appear1;
}
.show2{
  -webkit-animation-name: appear2;
          animation-name: appear2;
}
.show3{
  -webkit-animation-name: appear3;
          animation-name: appear3;
}
.hide{
  visibility: visible;
  -webkit-animation-name: disappear;
          animation-name: disappear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s; 
}
.hidden{
  visibility: hidden;
}
.middle-panel-main{
  width: 70vw; 
  margin-left: 15vw;
  display: inline-block;
  text-align: center;
  background-color: #09b;
  border-right: 2px solid black;
  border-left: 2px solid black;
  position: relative;
}
.contract{
  -webkit-animation-name: contract;
          animation-name: contract;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}


